.header-wrapper {
  background-image: url(../images/header-red-background-mobile-min.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    position: relative;
    min-height: 460px;

    @media screen and (min-device-pixel-retio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url(../images/header-red-background-mobile@2x-min.png);
    }
  }

  @media screen and (min-width: 768px) {
    min-height: 222px;
    background-image: url(../images/header-red-background-tablet-min.png);

    @media screen and (min-device-pixel-retio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url(../images/header-red-background-tablet@2x-min.png);
    }
  }

  @media screen and (min-width: 1280px) {
    min-height: 316px;
    background-image: url(../images/header-red-background-desktop-min.png);

    @media screen and (min-device-pixel-retio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url(../images/header-red-background-desktop@2x-min.png);
    }
  }
}

.header-nav {
  display: flex;
}

.search-wrapper {
  width: 100%;
  display: flex;
}

