.header__links-wrapper {
  display: flex;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    position: absolute;
  }
}

.header__link-home {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 26px;
  margin-left: 14px;
  margin-top: 14px;
  width: 87px;
  height: 34px;
  // background-color: #fff;
  // color: #000;
  border-radius: 100px;
  border: 0;
  text-decoration: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

  @media screen and (min-width: 768px) {
    padding: 12px 24px;
    margin-left: 16px;
    margin-top: 16px;
    width: 83px;
    height: 38px;
  }

  @media screen and (min-width: 1280px) {
    padding: 14px 32px;
    margin-left: 32px;
    width: 99px;
    height: 42px;
  }
}

