.header__link-library {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 19px;
  margin-left: 4px;
  margin-top: 14px;
  width: 113px;
  height: 34px;
  background-color: #fff;
  color: #000;
  border-radius: 100px;
  border: 0;
  text-decoration: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

  @media screen and (min-width: 768px) {
    width: 109px;
    height: 38px;
    padding: 12px 17px;
    margin-left: 4px;
    margin-top: 16px;
  }

  @media screen and (min-width: 1280px) {
    width: 117px;
    height: 42px;
    padding: 14px 21px;
    margin-left: 8px;
  }
}

.header__link-library:hover,
.header__link-library:focus {
  background-color: #000;
  color: #fff;
}

.header__link-library--current {
  background-color: #000;
  color: #fff;
  // background-color: #fff;
  // color: #000;
  // outline: #000 solid 3px;
}

.header__user-links-wrapper {
  display: flex;
  margin-left: auto;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    position: absolute;
    bottom: 24px;
    left: 50%;
    transform: translate(-50%, 0%);
  }
}

.header__link-watched {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 23px;
  width: 102px;
  height: 34px;
  background-color: #fff;
  color: #b92f2c;
  border-radius: 100px;
  border: 0;
  text-decoration: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

  @media screen and (min-width: 768px) {
    width: 102px;
    height: 38px;
    padding: 12px 23px;
    margin-top: 16px;
  }

  @media screen and (min-width: 1280px) {
    width: 117px;
    height: 42px;
    padding: 14px 32px;
  }
}

.header__link-watched:hover,
.header__link-watched:focus {
  background-color: #b92f2c;
  color: #fff;
}

.header__link-queue {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 24px;
  margin-left: 4px;
  width: 86px;
  height: 34px;
  background-color: #fff;
  color: #b92f2c;
  border-radius: 100px;
  border: 0;
  text-decoration: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

  @media screen and (min-width: 768px) {
    width: 86px;
    height: 38px;
    padding: 12px 24px;
    margin-top: 16px;
    margin-right: 16px;
  }

  @media screen and (min-width: 1280px) {
    width: 117px;
    height: 42px;
    padding: 14px 24px;
    margin-left: 8px;
    margin-right: 32px;
  }
}

.header__link-queue:hover,
.header__link-queue:focus {
  background-color: #b92f2c;
  color: #fff;
}

.logo-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px;

  @media screen and (min-width: 320px) {
    margin: 74px auto;
  }

  @media screen and (min-width: 768px) {
    margin-left: 16px;
    margin-bottom: 16px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 32px;
    margin-bottom: 32px;
  }
}

.header__logo {
  fill: transparent;
  stroke: #fff;
  width: 32px;
  height: 32px;

  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
  }

  @media screen and (min-width: 1280px) {
    width: 59px;
    height: 59px;
  }
}

.header__link-filmoteka {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 1.19;
  display: flex;
  align-items: center;
  text-align: center;
  margin-left: 8px;
  text-decoration: none;
  color: #ffffff;

  @media screen and (min-width: 768px) {
    font-size: 40px;
    line-height: 1.18;
  }

  @media screen and (min-width: 1280px) {
    font-size: 64px;
    line-height: 1.17;
    margin-left: 16px;
  }
}