/*Не редактировать (пока я не закончу работу)*/

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  &__backdrop {
      display: flex;
      height: 100%;
      width: 100%;
      background-color: rgba(0,0,0, 0.5);

      justify-content: center;
      align-items: center;
  }

  &__window {
      height: 568px;
      width: 806px;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 12px;

      background-color: #fff;
      position: relative;
  }
}

/*Для редактирования*/

.modal--show {
display: flex;
justify-content: center;
align-items: center;
}


